<template>
  <div class="intro">
    <h2 class="title">基于多任务学习的网络最短路径研究</h2>
    <h3 class="sub-title">意义</h3>
    <p>
      最短路径问题具有很强的直观背景，在网络最优化中有着重要的理论价值，同时被广泛地应用于社会生产生活中。<br>
      所谓的最短路径问题是指在一个网络赋权图上，找出一条从源点到汇点的路径，使得这两个节点间的距离最短。<br>
      这里的最短路径与我们平时生活中所理解的距离不同，不仅可以指那种地理上的距离，而且可以延伸到其他层面上那些能够被看作“路径”的度量，如流量，费用和时间等。<br>
      许多网络最优化问题最终可转化为最短路径问题，或者可用最短路的算法作为其子程序。而目前的研究工作主要集中于算法实现的优化改进与应用方面。
    </p>
    <h3 class="sub-title">研究内容</h3>
    <p>
      本课题主要研究内容为将多任务学习应用于寻找网络中的任意两点的最短路径。设计出适合在大规模网络中高效求解最短路径中的多个任务，
      通过多因子进化算法在任务优化过程中进行隐性知识转移，同时解决多个任务，以期提高最短路径算法的运行效率。
    </p>
    <h3 class="sub-title">目标函数</h3>
    <p>
      <img src="@/assets/formula.png">
      <br>
      M表示矩阵，l表示染色体的长度，g表示染色体。
    </p>
    <h3 class="sub-title">编码方式</h3>
    <p>
      使用一个n维的向量表示一条可行路径，染色体上的每个基因代表依次经过的节点，n不超过网络的维度。<br>
      染色体的第一个基因是起点，最后一个基因是终点。
    </p>
    <h3 class="sub-title">选择算子</h3>
    <ul>
      <li>轮盘赌</li>
      <p>
        轮盘赌选择策略的出发点是适应度值越好的个体被选择的概率越大。依据个体的适应度值计算每个个体在子代中出现的概率，并按照此概率随机选择个体构成子代种群，因此该方法也被称为适应度比例法。
      </p>
      <li>精英策略</li>
      <p>
        精英选择将一部分最优的候选解，原封不动的复制到下一代中。不过精英主义的问题在于有可能导致算法收敛到局部最优。 
      </p>
    </ul>
